body{margin:0;font-family:'Roboto', sans-serif;color:#000;overflow:hidden;overflow-x:auto}*{box-sizing:border-box}p,h1,h2,h3,h4,li,menu{margin:0;padding:0}textarea{padding:0;margin:0;width:100%;border:0;height:auto;height:initial;resize:none !important;overflow:hidden !important}:focus{outline:none}a:link{text-decoration:none;color:#000000}.item__hover:hover{cursor:pointer;background-color:#e8f0fe;border-radius:20px;border:0 !important}.unselectable{-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.simplebar-track.simplebar-vertical{width:12px !important}.simplebar-track{right:3px !important}.simplebar-scrollbar::before{background-color:#2a2a2a !important}.header__doc-text{font-size:20px;margin:0;margin-left:5px;color:#303030}.header__doc-search-list{position:absolute;top:45px;left:0;z-index:999;background-color:#fff;padding:10px 0;width:100%;box-shadow:0 2px 5px 2px rgba(60,64,67,0.15);border-bottom-left-radius:10px;border-bottom-right-radius:10px}.header__doc-search-list-item{padding:10px;cursor:pointer}.header__doc-search-list-item:hover{background-color:rgba(207,207,207,0.4)}.header__input-focus{background-color:#fff !important;box-shadow:rgba(60,64,67,0.15) 0px 2px 6px 2px;transition:background-color .2s linear, box-shadow .2s linear}.header__input-blur{background-color:rgba(192,192,192,0.2) !important;box-shadow:none;transition:background-color .2s linear, box-shadow .2s linear}.add__doc-plus{position:relative;display:inline-block;height:200px;width:150px;background-color:#fff;border-radius:5px;cursor:pointer}.add__doc-plus::after{content:'';position:absolute;top:0;left:0;height:100%;width:100%;background-image:url(data:image/png;base64,);background-position-x:50%;background-position-y:50%;transform:scale(0.4)}.add__doc-plus:hover{border:1px solid blue}.add__doc-title{font-size:15px;padding-left:5px;font-weight:normal}.header__doc-load-animate{transform:rotate(-360deg);-webkit-animation:load 2s infinite ease-in-out;animation:load 2s infinite ease-in-out}@-webkit-keyframes load{100%{transform:rotate(0deg)}}@keyframes load{100%{transform:rotate(0deg)}}.bold{font-weight:bold}.normalWeight{font-weight:normal}.italic{font-style:italic}.normalItalic{font-style:normal}.underline{text-decoration:underline}.noneUnderline{text-decoration:none}.header__doc-title{min-width:100px;font-size:18px;padding:0 10px;margin-right:10px;border:0}.header__doc-title:focus{border:2px solid #1a73e8 !important;border-radius:4px}.header__doc-title:hover{border:1px solid #e5e5e5}.page{background-color:#ffffff;font-size:16px;word-break:break-word}.page .image{position:relative;height:200px;width:200px;margin-right:auto;margin-bottom:5px;background-size:100% 100%;background-repeat:no-repeat}.page .image img{height:200px;width:200px}.page .activeImage{outline:2px solid #1344e6;z-index:999}.page .activeImage div:nth-child(1){position:absolute;top:50%;right:-5px;transform:translateY(-50%);height:10px;width:10px;z-index:999;background-color:#1344e6}.page .activeImage div:nth-child(2){position:absolute;bottom:-5px;right:-5px;height:10px;width:10px;z-index:999;background-color:#1344e6}.page .activeImage div:nth-child(3){position:absolute;bottom:-12px;right:50%;transform:translateY(-50%);height:10px;width:10px;z-index:999;background-color:#1344e6}.page .activeImage div:nth-child(4){position:absolute;top:50%;left:-5px;transform:translateY(-50%);height:10px;width:10px;z-index:999;background-color:#1344e6}

